<template>
        <vxe-table :data="tableData" v-bind="TableConfig">
            <vxe-column field="SerialNo" title="" sortable />
            <vxe-column field="ProductNo" title="" sortable />
            <vxe-column field="Printing" title="" sortable />
            <vxe-column field="LeadTimeDate" title="" sortable :formatter="formatDate" />
            <vxe-column field="ShippingTimeDate" title="" sortable :formatter="formatDate" />
            <vxe-column field="Destination" title="" sortable />
            <vxe-column field="DestinationArea" title="" sortable />
            <vxe-column field="ReceivingPoint" title="" sortable />
            <vxe-column field="BackNo" title="" sortable />
            <vxe-column field="OrderNo" title="" sortable />
            <vxe-column field="PlineNo" title="Pline" sortable />
            <vxe-column field="ReceivingNo" title="" sortable />
        </vxe-table>
</template>
<script setup>
import { defineProps } from 'vue';
import { TableConfig } from '@/service/GetDataMethod/utils'
import XEUtils from 'xe-utils'

defineProps({
    tableData: Array,
})

const formatDate = ({ cellValue }) => {
    return XEUtils.toDateString(cellValue, 'yyyy-MM-dd HH:mm:ss')
}
</script>