<script setup>
import { defineProps, ref } from 'vue';
import BasicTemp from './BasicTemp.vue';
import PokayokeDate from './PokayokeDate.vue';

// import vw_MotorTemp from '../Template/vw_MotorTemp.vue';
// import vw_GearTable from '../Template/vw_GearTable.vue';
// vw_GearTable
// import vw_Rotor1Temp from '../Template/vw_Rotor1Temp.vue';
// import vw_Rotor2Temp from '../Template/vw_Rotor2Temp.vue';
// import vw_RRTemp from '../Template/vw_RRTemp.vue';
import { TableConfig } from '@/service/GetDataMethod/utils';
import { vw_GearTable, vw_MotorTemp, vw_Rotor1Temp, vw_Rotor2Temp, vw_RRTemp } from '@/service/Import/tableTemp';
import XEUtils from 'xe-utils'


// import { forEach } from 'core-js/core/array';
// import { ElMessageBox } from "element-plus"
defineProps({
    tableData: Array,
})

//
const columns = [
    "Motor",
    "Gear",
    "RotorT1",
    "RotorT2",
    "Rr"
]

//
const showMotor = ref(false),
    showGear = ref(false),
    showRotor1 = ref(false),
    showRotor2 = ref(false),
    showRR = ref(false);

//
const checkAll = ref(false),
    isIndeterminate = ref(false),
    checkedCities = ref([])

//
const handleCheckAllChange = (val) => {
    checkedCities.value = val ? columns : []
    isIndeterminate.value = true
    handleCheckedCitiesChange(checkedCities.value)
    //showAll(checkedCities);
}

//
const handleCheckedCitiesChange = (value) => {
    //console.log(value);
    const checkedCount = value.length
    checkAll.value = checkedCount === columns.length
    isIndeterminate.value = checkedCount > 0 && checkedCount < columns.length
    // false
    cancelMoth(value)
    showTable(value)
}

//
const showTable = (tableNameList) => {
    //console.log(tableNameList);
    tableNameList.forEach(key => {
        switch (key) {
            case 'Motor':
                showMotor.value = true
                break;
            case 'Gear':
                showGear.value = true
                break;
            case 'RotorT1':
                showRotor1.value = true
                break;
            case 'RotorT2':
                showRotor2.value = true
                break;
            case 'Rr':
                showRR.value = true
                break;
            default:
                break;
        }
    })
}

//
const cancelMoth = (valueList) => {
    // 
    columns.forEach(key => {
        if (!valueList.includes(key)) {
            // checkedCities
            const index = checkedCities.value.indexOf(key);
            if (index !== -1) {
                checkedCities.value.splice(index, 1);
            }

            // false
            switch (key) {
                case 'Motor':
                    showMotor.value = false;
                    break;
                case 'Gear':
                    showGear.value = false;
                    break;
                case 'RotorT1':
                    showRotor1.value = false;
                    break;
                case 'RotorT2':
                    showRotor2.value = false;
                    break;
                case 'Rr':
                    showRR.value = false;
                    break;
                default:
                    break;
            }
        }
    })
}

const formatDate = ({ cellValue }) => {
    return XEUtils.toDateString(cellValue, 'yyyy-MM-dd HH:mm:ss')
}

</script>