//
export function exportData(TableData, fieidData) {
  let dataIndex = 0;
  const newObj = TableData.map((table) => {
    const ColVal = table.colName.map((col) => {
      if (dataIndex < fieidData.length) {
        const data = fieidData[dataIndex++];
        return {
          col: col,
          val: data
        };
      } else {
        return null;
      }
    });

    return {
      title: table.tableName,
      val: ColVal
    };
  });
  return newObj;
}

