export const addTwoItem = (count, name) => {
  let itemList = []
  itemList.push(name + "")
  for (var i = 0; i < count; i++) {
    itemList.push(`${name}${i + 1}`);
    itemList.push(`${name}${i + 1} `);
  }
  itemList.push(name + "A/B")
  return itemList
};