//
export const realList = (requestData) => {
  return new Promise((resolve) => {
    setTimeout(async () => {
      try {

        TableConfig.loading = true;
        let response;
        response = await apiClient.post(
          // "api/DBTest/SelTablePost",
          "api/DBTest/getTableData",
          requestData
        );
        if (response.data != "") {
          // pagerConfig.total = response.data.count;
          TableConfig.loading = false;
          // Indicates that the asynchronous operation is complete
        } else {
          TableConfig.loading = false;
          ElMessage({
            type: "info",
            message: ``,
          }); // 
        }
        resolve(response.data);
      } catch (error) {
        ElMessageBox.alert(error, "", {
          type: "info",
          confirmButtonText: "",
          callback: () => {
            ElMessage({
              type: "error",
              message: error,
            }); // 

            TableConfig.loading = false;
          },
        });
      }
    }, 100);
  });
};

//
export const exportData = async (tableName) => {
  try {
    const response = await ElMessageBox.confirm(""); // 
    if (response === "confirm") { // 
      const loadings = ElLoading.service({ // 
        lock: true,
        text: "...",
      });
      const response = await apiClient.get( // 
        `api/Excel/ExportData?tableName=${tableName}`,
        {
          responseType: "blob", // 'blob'
        }
      );
      const blob = new Blob([response.data], { // Blob
        type: response.headers["content-type"],
      });
      const url = window.URL.createObjectURL(blob); // URL

      // <a>
      const link = document.createElement("a");
      let filename = `${tableName}.xlsx`;
      let count = 1;

      // 
      while (document.querySelectorAll(`[download="${filename}"]`).length > 0) {
        filename = `${tableName}_${count}.xlsx`;
        count++;
      }

      link.href = url;
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      // 
      window.URL.revokeObjectURL(url);
      document.body.removeChild(link);
      loadings.close(); // 
    }
  } catch (error) {
    console.error(":", error);
    // 
  }
};



